@import url(https://fonts.googleapis.com/css?family=Raleway);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  min-height: 100vh;
  display: flex;
  flex: 1 1;
  flex-direction: row;
 
}
.main {
    display: flex;
    flex: 1 1;
    height : 100vh;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    /* background: url("../assets/bg2.jpg") center; */
    align-items: center;
    background: -webkit-linear-gradient(320deg, #000000, #221e27, #28293a, #ca003b, #982048);
    background: linear-gradient(130deg, #000000, #221e27, #28293a, #ca003b, #982048);
    background-size: 1000% 1000%;

    -webkit-animation: AnimationName 11s ease infinite;
    animation: AnimationName 11s ease infinite;
}

.scrollerContainer{
    display: flex;
    flex: 1 1;
    width: 100%;
    height: 100%;
    justify-content: center;
    align-items: center;
    background-size: cover;
    outline: none;
}

div{
    outline: none;
}


@-webkit-keyframes AnimationName {
    0%{background-position:63% 0%}
    50%{background-position:38% 100%}
    100%{background-position:63% 0%}
}
@keyframes AnimationName { 
    0%{background-position:63% 0%}
    50%{background-position:38% 100%}
    100%{background-position:63% 0%}
}
.navbar {
    width: 10%;
    height: 100vh; 
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: transparent;
}
@font-face {
    font-family: 'Neoneon';
    font-style: normal;
    src: url(/static/media/Neoneon.696ade59.otf);
}

div.container
{
    font-family: 'Neoneon';
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    border-radius: 3ch;
}

div.container a
{
    text-decoration: none;
    font: 25px "Neoneon";
    margin: 0px 10px;
    padding: 10px 10px;
    position: relative;
    z-index: 0;
    cursor: pointer;
    color: white;
}

div.highlightTextOut a:before, div.highlightTextIn a:before
{
    position: absolute;
    color: rgb(236, 106, 219);
    overflow: hidden;
    content: attr(alt);
    -webkit-transition: all 0.3s;
    transition: all 0.3s;
    -webkit-transform: scale(0.8);
            transform: scale(0.8);
    opacity: 0;
}

a:active {
    color: rgb(236, 106, 219);
    -webkit-transition: -webkit-transform 0.5s;
    transition: -webkit-transform 0.5s;
    transition: transform 0.5s;
    transition: transform 0.5s, -webkit-transform 0.5s;
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}

div.highlightTextOut a:hover:before, div.highlightTextIn a:hover:before
{
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 1;
}

.img{
    -webkit-transition: -webkit-transform 0.5s;
    transition: -webkit-transform 0.5s;
    transition: transform 0.5s;
    transition: transform 0.5s, -webkit-transform 0.5s;
    width: 10vh;
    margin: 10px;
}

.img:hover{
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}

.icon{
    margin: 1vh
}

.icon:hover{
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}
.reactPageScrollerComponent {
    display: flex;
    flex: 1 1;
    background-color: transparent;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    position: relative;
    border-radius: 2ch;
}
.first-component{
    background-color: transparent;
}

h2{
    color: white;
}
